import { FunctionComponent } from 'preact';
import { ChangeEvent, HTMLAttributes, useRef, useState } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import { Check } from '@adobe/elsie/icons';
import { Icon } from '@adobe/elsie/components/Icon';
import '@adobe/elsie/components/Checkbox/Checkbox.css';

export interface CheckboxProps
  extends Omit<HTMLAttributes<HTMLInputElement>, 'size'> {
  name: string;
  size?: 'medium' | 'large';
  disabled?: boolean;
  error?: boolean;
  label?: string;
  description?: string;
}

export const Checkbox: FunctionComponent<CheckboxProps> = ({
  name,
  value,
  size = 'medium',
  disabled = false,
  error = false,
  label = '',
  description = '',
  className,
  checked,
  ...props
}) => {
  const [isChecked, setIsChecked] = useState(
    checked === undefined ? false : checked
  );
  const ref = useRef<HTMLInputElement>(null);

  const handleCheckboxChange = (e: ChangeEvent<HTMLInputElement>) => {
    props.onChange?.(e);
    setIsChecked(e.currentTarget.checked);
  };

  const handleOnKeyDown = (e: KeyboardEvent) => {
    if (e.key === ' ') {
      e.preventDefault();
      ref?.current?.click();
    }
  };

  return (
    <label
      className={classes([
        'dropin-checkbox',
        ['dropin-checkbox--disabled', disabled],
      ])}
    >
      <input
        ref={ref}
        name={name}
        value={value}
        type="checkbox"
        disabled={disabled}
        className={classes([
          'dropin-checkbox__checkbox',
          ['dropin-checkbox__checkbox--error', error],
          className,
        ])}
        {...props}
        onChange={handleCheckboxChange}
        checked={isChecked}
      />
      <span
        aria-checked={isChecked ? 'true' : 'false'}
        aria-labelledby={`${name}-label`}
        aria-describedby={`${name}-description`}
        className={classes([
          'dropin-checkbox__box',
          ['dropin-checkbox__box--error', error],
          ['dropin-checkbox__box--disabled', disabled],
        ])}
        role="checkbox"
        tabIndex={disabled ? -1 : 0}
        onKeyDown={handleOnKeyDown}
      >
        <Icon
          className={classes(['dropin-checkbox__checkmark'])}
          source={Check}
          size="16"
          stroke="3"
        />
      </span>
      <div
        id={`${name}-label`}
        className={classes([
          'dropin-checkbox__label',
          `dropin-checkbox__label--${size}`,
          ['dropin-checkbox__label--disabled', disabled],
        ])}
      >
        {label}
      </div>
      <div />
      <div
        id={`${name}-description`}
        role={'note'}
        className={classes([
          'dropin-checkbox__description',
          `dropin-checkbox__description--${size}`,
          ['dropin-checkbox__description--disabled', disabled],
        ])}
      >
        {description}
      </div>
    </label>
  );
};
