import { FunctionComponent } from 'preact';
import { classes } from '@adobe/elsie/lib';
import { Checkbox, CheckboxProps } from '@adobe/elsie/components';
import { BillToShippingAddressSkeleton } from '@/checkout/components/BillToShippingAddress';
import '@/checkout/components/BillToShippingAddress/BillToShippingAddress.css';
import { useText } from '@adobe/elsie/i18n';

export interface BillToShippingAddressProps
  extends Omit<CheckboxProps, 'name' | 'label'> {
  isInitialized?: boolean;
}

export const BillToShippingAddress: FunctionComponent<
  BillToShippingAddressProps
> = ({ className, isInitialized = true, checked = true, ...props }) => {
  const translations = useText({
    title: 'Checkout.BillToShippingAddress.title',
  });

  if (!isInitialized) return <BillToShippingAddressSkeleton />;

  return (
    <div className={classes(['checkout-bill-to-shipping-address', className])}>
      <Checkbox
        data-testid="bill-to-shipping-checkbox"
        className="checkout-bill-to-shipping-address__checkbox"
        checked={checked}
        name="checkout-bill-to-shipping-address__checkbox"
        label={translations.title}
        {...props}
      />
    </div>
  );
};
