import { FormFields, setBillingAddress } from '@/checkout/api';
import { BillToShippingAddress as BillToShippingAddressComponent } from '@/checkout/components/BillToShippingAddress';
import {
  IsBillToShipping,
  cartSignal,
  isBillToShippingSignal,
} from '@/checkout/signals';
import { Container } from '@adobe/elsie/lib';
import { Signal } from '@preact/signals-core';
import { useEffect, useState } from 'preact/compat';
import { BILL_TO_SHIPPING_KEY } from './constants';
import { useAddressFormFields } from '@/checkout/context/address-form-fields';
import { CheckboxProps } from '@adobe/elsie/src/components';
import {
  ShippingAddress,
  BillingAddress,
} from '@/checkout/data/models/address';

type Address = BillingAddress | ShippingAddress[];

function compareFields(first: Address, second: Address, field: keyof Address) {
  const firstValue = first[field];
  const secondValue = second[field];

  if (firstValue === undefined && secondValue === undefined) return true;
  if (firstValue === null && secondValue === null) return true;

  if (typeof firstValue === 'object' && typeof secondValue === 'object') {
    return JSON.stringify(firstValue) === JSON.stringify(secondValue);
  }

  return firstValue === secondValue;
}

export function compareAddresses(
  addressFormFields: FormFields,
  first: BillingAddress | undefined,
  second: ShippingAddress | undefined
) {
  if (!first && !second) return true;
  if (!first || !second) return false;

  return addressFormFields.every((field) => {
    const fieldCode = field.code as keyof Address;
    return compareFields(first, second, fieldCode);
  });
}

const useInitialState = ({
  isBillToShipping,
}: {
  isBillToShipping: Signal<IsBillToShipping>;
}) => {
  const [isInitialized, setIsInitialized] = useState(false);
  const { fields } = useAddressFormFields();
  const cartData = cartSignal.value.data;
  const hasCartData = !!cartData;
  const billingAddress = cartData?.billingAddress;
  const shippingAddress = cartData?.shippingAddresses?.[0];

  useEffect(() => {
    if (!fields) return;

    const localIsBillToShipping = localStorage.getItem(BILL_TO_SHIPPING_KEY);

    if (!isInitialized && localIsBillToShipping) {
      setIsInitialized(true);
      isBillToShipping.value = {
        checked: localIsBillToShipping === 'true',
        setByUser: false,
      };
      return;
    }

    if (!isInitialized && hasCartData) {
      setIsInitialized(true);

      const isSameShippingAndBilling = compareAddresses(
        fields,
        billingAddress,
        shippingAddress
      );

      isBillToShipping.value = {
        checked: cartData?.billingAddress
          ? isSameShippingAndBilling
          : isBillToShipping.value.checked,
        setByUser: false,
      };
    }
  }, [
    billingAddress,
    cartData?.billingAddress,
    fields,
    hasCartData,
    isBillToShipping,
    isInitialized,
    shippingAddress,
  ]);

  return { isInitialized };
};

export interface BillToShippingAddressProps
  extends Omit<CheckboxProps, 'name' | 'label'> {}

export const BillToShippingAddress: Container<BillToShippingAddressProps> = ({
  children,
  ...props
}) => {
  const { data: cartData } = cartSignal.value;
  const cartId = cartData?.id || '';
  const isBillToShipping = isBillToShippingSignal.value.checked;

  const { isInitialized } = useInitialState({
    isBillToShipping: isBillToShippingSignal,
  });

  const onChangeHandler = (event: Event) => {
    const checkbox = event.target as HTMLInputElement;
    const isChecked = checkbox.checked;
    const cartData = cartSignal.value.data;
    const hasShippingAddress = Boolean(cartData?.shippingAddresses?.[0]);

    isBillToShippingSignal.value = { checked: isChecked, setByUser: true };

    localStorage.setItem(BILL_TO_SHIPPING_KEY, isChecked.toString());

    if (isInitialized && isChecked && hasShippingAddress) {
      const controller = new AbortController();

      setBillingAddress({
        signal: controller.signal,
        cartId,
        input: { same_as_shipping: true },
      }).catch((error) => {
        console.error(error);
      });

      return () => {
        controller.abort();
      };
    }
  };

  return (
    <BillToShippingAddressComponent
      {...props}
      checked={isBillToShipping}
      isInitialized={isInitialized}
      onChange={onChangeHandler}
      disabled={cartSignal.value.pending}
    />
  );
};
